* {
  font-family: "Montserrat", sans-serif;
}

.main-container {
  font-family: "Montserrat", sans-serif;
  margin: 15vh auto;
  display: block;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
  overflow: hidden;
  width: 768px;
  max-width: 100%;
  min-height: 480px;
}

.form-container h1 {
  font-weight: bold;
  font-size: 60px;
  margin: 0;
  margin-bottom: 15px;
}

.main-container p {
  font-size: 14px;
  font-weight: 100;
  line-height: 20px;
  letter-spacing: 0.5px;
  margin: 20px 0 30px;
}

.form-container h3 {
  margin-bottom: 30px;
}

.main-container button {
  border-radius: 20px !important;
  border: 1px solid #8a41ff;
  background-color: #8a41ff;
  color: #ffffff;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 45px;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-top: 0;
}

.main-container button:focus {
  outline: none;
}

.main-container form {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  text-align: center;
}

.main-container input {
  background-color: #eee;
  border: none;
  padding: 12px 15px;
  margin: 8px 0;
  width: 100%;
}

.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
}

.sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
}

.sence-logo {
  width: 150px;
}

.overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}

.overlay {
  background: #414eff;
  background: linear-gradient(to right, #8a41ff, #8a41ff);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 0 0;
  color: #ffffff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
}

.overlay-panel p {
  margin-top: 0;
}

.overlay-right {
  right: 0;
}

@media (max-width: 820px) {
  .overlay-container {
    display: none;
  }

  .form-container {
    position: relative;
    display: block;
    min-width: 100%;
    margin-top: 60px;
    color: #ffffff;
  }

  .form-container h1 {
    font-size: 40px;
  }

  .main-container {
    width: 90%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    background: rgb(1, 126, 146);
    background: linear-gradient(
      90deg,
      rgba(1, 126, 146, 1) 30%,
      rgba(116, 48, 234, 1) 78%
    );
  }

  .form-container form {
    background: transparent;
    padding: 0;
    width: 90%;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  .form-container button {
    padding: 10px 15px;
    background-color: transparent;
    color: #ffffff;
    border: 2px solid #ffffff;
  }
}

